.menu-item{
    min-width: 30%;
    height: 240px;
    flex: 1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 0px solid black;
    margin: 0 15px 15px;
    background-position: center;
    background-size: cover;
    overflow: hidden;
    border-top-left-radius: 50px;
    // border-end-end-radius: 10%;
    // border-start-start-radius: 10%;
    
}
.menu-item:hover{
    cursor: pointer; 
}
.menu-item .background-image:hover{
    transition: scale(1,1);
    transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
}
.menu-item .content:hover{
    opacity: 0.9;
}


.menu-item.large{
    height: 380px;
}
.menu-item:first-child{
    margin-right: 7.5px;
}
.menu-item:last-child{
    margin-left: 7.5px
    ;
}
.content{
    height: 90px;
    padding: 0 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 0px solid black;
    background-color: white;
    opacity: 0.7;
    position: absolute;
}

.title{
    font-weight: bold;
    margin-bottom: 6px;
    font-size: 22px;
    color: #4a4a4a;
}
.subtitle{
    font-weight: lighter;
    font-size: 16px;
    color:black;
}

.background-image{
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
}
.background-image:hover{
    opacity:0.4;
}